import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router } from '@angular/router';

import { DxMenuComponent } from 'devextreme-angular';
import List from 'devextreme/ui/list';
import { VatService } from '@modules/vat/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Library, Helper, AuthService, VPreviewInvService, VStringService, sEnum } from '@core/index';
import { ViewAttachFileComponent } from '@shared/modules/vat-shared/components/view-attach-file/view-attach-file.component';
import * as moment from 'moment';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import ArrayStore from 'devextreme/data/array_store';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
@Component({
  selector: 'app-send-list-email',
  templateUrl: './send-list-email.component.html',
  styleUrls: ['./send-list-email.component.scss']
})
export class SendListEmailComponent implements OnInit {
  selectedItems: Array<any> = [];
  _language: string;
  _translate: any;
  ganXong = false;
  clickCurent = '';
  disEditMulti = true;
  invoiceList: any;
  invoiceIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  showPopup = false;
  totalInvoice: number;
  invoices: any;
  invoice_list: ArrayStore;
  isPXKDaiLy = false;
  loaiHoaDon:any;
  configs: any;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private vatService: VatService,
    public translate: TranslateService,
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private customersService: CustomersService,
    private vPreviewInvService : ViewInvoiceService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }

  ngOnInit() {
    if (this.loaiHoaDon == sEnum.loaihoadon.HGDL04)
      this.isPXKDaiLy = true;
    this.convertData();
    this.configs = this.authService.getConfigs();
    this.invoice_list = new ArrayStore({
      key: 'id',
      data: this.invoiceList
    });
  }

  convertData() {
    this.invoiceList.forEach(item => {
      if (!item.buyer_email) {
        this.customersService.getCustomer(item.customer_id).subscribe(response => {
          let customer = response.data.customer;
          if (customer) {
            item.buyer_email = customer.email;
          }
        });
      }
    });
  }
  _validGrid() {
    let _isvalid = true;
    this.invoiceList.forEach((element, index) => {
      if (VStringService._getValueToString(element.to_warehouse_name) == "") {
        Library.notify(this._translate.CONTROLL.LABEL.name_receiver + " " + this._translate.VALIDATION.required, "error");
        _isvalid = false;
        return false;
      }
      if (VStringService._getValueToString(element.buyer_email) == "") {
        Library.notify("Email " + this._translate.VALIDATION.required, "error");
        _isvalid = false;
        return false;
      }
    });
    return _isvalid;
  }
  onEditorPreparing(e) {
    var grid = e.component;
    if (e.parentType === 'dataRow') {
      var oldOnValueChanged = e.editorOptions.onValueChanged;
      e.editorOptions.onFocusOut = (args) => {
        this.invoice_list.update(e.row.data.id, e.row.data);
        //  grid.cellValue(e.row.rowIndex, e.index, e.row.data.to_warehouse_name);
      };
    }
  }

  onEditorPrepared(e) {
    if (e.dataField == 'buyer_email' && e.parentType == 'dataRow') {
      //   e.row.cells[6].text = "htkt";
      //  e.editorOptions.onValueChanged = function(args){
      //    e.row.cells[6].value = args.value;
      //       e.setValue(args.value);
      //  }
    }
  }

  onEditingStart(e) {
    e.component.saveEditData();
  }

  view(data) {
    if (data) {
      let ids = [];
      if (data) {
        ids.push(data.id);
      }
      if (this.configs.system_parameter.view_multi_invoice) {
        let data = this.vatService.taodulieuXemMau(ids);
        data['configs'] = sEnum.cauhinhxemmau.hoadon;
        this.vPreviewInvService.xemMau(data);
      }
      else {
        const initialState = {
          items: ids,
          isXuatHD: false,
          hinhthucxuat: '',
          typeAction: sEnum.typeCode.xem_hoa_don,
          type_invocie: sEnum.typeCode.phieu_xuat_kho
        };
        this.vPreviewInvService.xemMauPhanTrang(initialState);
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  dong() {
    this.bsModalRef.content.successSaveItem = 'success';
    this.bsModalRef.hide();
  }

  dongy() {
    if (this._validGrid()) {
      Library.showloading();
      this.invoices = this.invoiceList.filter(item => item.buyer_email);
      let data: any = [];
      this.invoices.forEach(item => {
        let customer_name = item.to_warehouse_name;
        if (this.loaiHoaDon == sEnum.loaihoadon.HGDL04)
          customer_name = item.customer_name;
        data.push({ id: item.id, email: item.buyer_email, customer_name: customer_name });
      });
      let sub = this.vatService.sendMail(data).subscribe(rs => {
        if (rs.status == 'success') {
          Library.notify('Gi mail thnh cng', 'success');
          this.bsModalRef.content.successSaveItem = 'success_mail';
          this.bsModalRef.hide();
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  }

  selectionChanged(data: any) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    this.disEditMulti = true;
    this.selectedItems = data.selectedRowsData;
    if (this.selectedItems.length > 0) {
      let checks = this.selectedItems.filter((item) => {
        return item.number_of_vouchers;
      });
      let checkDaXuat = this.selectedItems.filter((item) => {
        return item.status == 'DA_XUAT';
      });
      if (checks.length == this.selectedItems.length && checkDaXuat.length == 0) {
        this.disEditMulti = false;
      }
    }
  }

}
