  /** Kt thc */

  roundNumber(number, n): number {
    if (number == null) {
      return number;
    } else {
      let p = Math.pow(10, n);
      return Math.round(number * p) / p;
    }
  }

  getFormatterToNumber(formatter) {
    let s1 = formatter.replace("0,###", "");
    if (s1 == "") {
      return 0;
    } else {
      let s2 = s1.replace(".", "");
      return s2.split("").length;
    }
  }

  formatDecimalColumn(number) {
    if (number == 0) {
      return '0,###';
    } else {
      return '0,###.' + '0'.repeat(number);
    }
  }

  inChuyenDoiText() {
    if (this.frmData.time_print_conversion > 0) {
      return this._translate.CONTROLL.BUTTON.view_conversion;
    } else {
      return this._translate.CONTROLL.BUTTON.print_conversion;
    }
  }

  //s kin form
  totalPaymentChange = (e) => {
    this.onNumberChange();
    let currency_code = this.frmData.currency_code;
    if (this.configs.system_parameter.view_price_nte)
      currency_code = "VND";
    let tien_chu = Helper.tienBangChu(Math.abs(e.value), VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);
    this.frmData.total_payment_in_word = this.invoiceService.docTienAm(e.value, tien_chu, this.configs, currency_code);
  }

  preview() {
    setTimeout(() => {
      this.handleFormData();
      if (!this._validForm(true))
        return;
      let data = this.vatService.taodulieuXemTruocMau(this.frmData);
      data['configs'] = sEnum.cauhinhxemmau.hoadon;
      this.vPreviewInvService.xemMau(data);
    }, 300);
  }
  //#region "Lnh iu ng"
  /* K lnh iu ng */
  submitlenhdieudong() {
    let checkValid = this.formInvoice.form.instance.validate();
    if (!checkValid.isValid) {
      return;
    }
    if (this.windowPreviewBeforeSubmit) {
      this.windowPreviewBeforeSubmit.focus();
      return;
    }
    if (!this.isCheckLenhDieuDong)
      this.resultCompareData = this.checkDataFormChange(this.valueChange, this.frmData);
    setTimeout(() => {
      this.handleFormData();
      if (!this._validForm(false))
        return;
      if (this.isCheckLenhDieuDong) {
        this.kylenhdieudong();
        this.isCheckLenhDieuDong = false;
      } else {
        this.Reviewlenhdieudong();
      }
    }, 300);
  }
  /* Thc hin k lnh iu ng */
  kylenhdieudong() {
    let tempFrmData = this.copyFrmData();
    if (this.frmData.id) {
      let ids = [];
      ids.push(this.frmData.id);
      if (!this.resultCompareData) {
        Library.showloading();
        if (!this.isTT78) {
          let sub = this.vatService.edit(this.frmData.id, tempFrmData).subscribe(rs => {
            if (rs.status == 'success') {
              this.frmData.id = rs.data.id;
              this.getDataToCompare();
              const sub = this.vatService.kylenhdieudong(ids).subscribe(rs => {
                if (rs.status === 'success') {
                  Library.notify(this._translate.NOTIFY.sign_the_dispatch_success, 'success');
                  this.isShowHuyLenhDD = true;
                  this.disablleScreen('');
                } else {
                  Library.notify(rs.message, 'error');
                }
              });
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => { }, () => {
            Library.hideloading();
            sub.unsubscribe();
          });
        }
        else {
          let sub = this.vatService.editTT78(this.frmData.id, tempFrmData).subscribe(rs => {
            if (rs.status == 'success') {
              this.frmData.id = rs.data.id;
              this.getDataToCompare();
              const sub = this.vatService.kylenhdieudong(ids).subscribe(rs => {
                if (rs.status === 'success') {
                  Library.notify(this._translate.NOTIFY.sign_the_dispatch_success, 'success');
                  this.isShowHuyLenhDD = true;
                  this.disablleScreen('');
                } else {
                  Library.notify(rs.message, 'error');
                }
              });
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => { }, () => {
            Library.hideloading();
            sub.unsubscribe();
          });
        }
      }
      else {
        Library.showloading();
        const sub = this.vatService.kylenhdieudong(ids).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.NOTIFY.sign_the_dispatch_success, 'success');
            this.isShowHuyLenhDD = true;
            this.disablleScreen('');
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      }
    }
    else {
      Library.showloading();
      if (!this.isTT78) {
        let sub = this.vatService.ghiTam(tempFrmData).subscribe(rs => {
          if (rs.status == 'success') {
            this.frmData.id = rs.data.id;
            let ids = [];
            ids.push(this.frmData.id);
            const sub = this.vatService.kylenhdieudong(ids).subscribe(rs => {
              if (rs.status === 'success') {
                Library.notify(this._translate.NOTIFY.sign_the_dispatch_success, 'success');
                this.isShowHuyLenhDD = true;
                this.disablleScreen('');
              } else {
                Library.notify(rs.message, 'error');
              }
            });
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      }
      else {
        let sub = this.vatService.ghiTamTT78(tempFrmData).subscribe(rs => {
          if (rs.status == 'success') {
            this.frmData.id = rs.data.id;
            let ids = [];
            ids.push(this.frmData.id);
            const sub = this.vatService.kylenhdieudong(ids).subscribe(rs => {
              if (rs.status === 'success') {
                Library.notify(this._translate.NOTIFY.sign_the_dispatch_success, 'success');
                this.isShowHuyLenhDD = true;
                this.disablleScreen('');
              } else {
                Library.notify(rs.message, 'error');
              }
            });
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      }
    }
  }


  /**
   * Copy object
   */

  copyFrmData() {
    let frmTemp = _.cloneDeep(this.frmData);
    if (frmTemp.invoice_options && frmTemp.invoice_options.length > 0) {
      frmTemp.invoice_options = frmTemp.invoice_options.map((item) => {
        return {
          invoice_id: item.invoice_id ? item.invoice_id : null,
          code: item.code ? item.code : null,
          name: item.name ? item.name : null,
          value: item.value ? item.value : null,
          data_type: item.data_type ? item.data_type : null,
        };
      })
    }
    return frmTemp;
  }

