  /* In lnh iu ng */
  inlenhdieudong() {
    if (!this.frmData.id || !this.isShowHuyLenhDD) {
      setTimeout(() => {
        this.handleFormData();
        if (!this._validForm(true))
          return;
        let w = 1920, h = 900;
        w = window.screen.availWidth * 100 / 100;
        h = window.screen.availHeight * 93 / 100;
        let win = window.open("about:blank", "name", "width=200, height=100");
        win.document.body.innerHTML = this._translate.NOTIFY.loading;
        win.blur();
        Library.showloading();
        let sub = this.vatService.xemtruoclenhdieudong(this.frmData).subscribe(rs => {
          if (rs.status == 'success') {
            let pdf_file = rs.data.file_content;
            let objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
            win.resizeTo(screen.availWidth, screen.availHeight);
            win.focus();
            win.open(objectUrl, "name", "width=" + w + ", height=" + h);
          } else {
            win.close();
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      }, 300);
    }
    else {
      let ids = [];
      ids.push(this.frmData.id);
      let w = 1920, h = 900;
      w = window.screen.availWidth * 100 / 100;
      h = window.screen.availHeight * 93 / 100;
      let win = window.open("about:blank", "name", "width=200, height=100");
      win.document.body.innerHTML = this._translate.NOTIFY.loading;
      win.blur();
      Library.showloading();
      let sub = this.vatService.xemlenhdieudong(ids).subscribe(rs => {
        if (rs.status == "success") {
          let pdf_file = rs.data[0].file_content;
          let objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
          win.resizeTo(screen.availWidth, screen.availHeight);
          win.focus();
          win.open(objectUrl, "name", "width=" + w + ", height=" + h);
        } else {
          win.close();
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  }
  /* Hy lnh iu ng */
  huylenhdieudong() {
    const setInvoiceDate = Library.confirm(this._translate.NOTIFY.warning_the_dispatch_success, this._translate.NOTIFY.notify);
    setInvoiceDate.then((dialog) => {
      if (!dialog) {
      }
      else {
        Library.showloading();
        let ids = [];
        ids.push(this.frmData.id);
        let sub = this.vatService.huylenhdieudong(ids).subscribe(rs => {
          if (rs.status == 'success') {
            Library.notify(this._translate.NOTIFY.cancel_the_dispatch_success, 'success');
            this.isShowHuyLenhDD = false;
            this.enableScreen();
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      }
    });
  }

