import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { NgxPaginationModule } from 'ngx-pagination';
import { VatService } from '@modules/vat/shared';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { VStringService, SocketService, AuthService, sEnum, Library, Helper, VChanggeMoneyService, VPreviewInvService } from '@core/index';
declare var $: any;

@Component({
  selector: 'app-view-delivery',
  templateUrl: './view-delivery.component.html',
  styleUrls: ['./view-delivery.component.scss']
})
export class ViewDeliveryComponent implements OnInit {
  bsModalSignRef: BsModalRef;
  items: any;
  key: string = 'name';
  reverse: boolean = false;
  objectUrl:any;
  mainTest:any;
  _language: string;
  _translate: any;
  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }
  p: number = 1;
  constructor(
    public bsModalImportRef: BsModalRef,
    private vatService: VatService,
    private router: Router,
    public translate: TranslateService,
    private authService: AuthService,
  ) { 
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }

  ngOnInit() {
    this.getPage(1, 'desc', 'created');
  }
  viewTransport(item) {
    let id = item.id;
  }

  getPage(page: number, sort: string = 'desc', order: string = 'created') {
   
    this.p = page;
    let oTransport: any;
    let index = 1;
    for (let item of this.items) {
      if (index == page) {
        oTransport = item;
        break;
      }
      index = index + 1;
    }
    Library.showloading();
    let sub = this.vatService.xembienbangiaohang(oTransport.id).subscribe(rs => {
      if (rs.status == 'success') {
        this.objectUrl = rs.data.file_content;
        this.mainTest = "data:application/pdf;base64," + this.objectUrl;

        let iframe = document.getElementById('preview-iframe');
      
        iframe.setAttribute("src", this.mainTest);
        // this.objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
        // let ts = this.objectUrl;
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  dong() {
    this.router.navigate(['/system/transport-invoice']);
    this.bsModalImportRef.hide()
  }
  dongy()
  {
    let ids = [];
    for (let item of this.items) {
           ids.push(item.id);
      }
    Library.showloading();
    let sub = this.vatService.taibienbangiaohang(ids).subscribe(rs => {
      if (rs.status == 'success') {
        Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
}
