        invoice["unit_name"] = element.unit_name ? element.unit_name : "";
        invoice["quantity"] = element.quantity ? Helper.toNumber(element.quantity) : null;
        invoice["number_real_export"] = element.number_real_export ? Helper.toNumber(element.number_real_export) : null;
        invoice["number_real_import"] = element.number_real_import ? Helper.toNumber(element.number_real_import) : null;
