  onNumberOfVouchers(e, value) {
    let numberOfVouchers = value;
    if (e.row.key.number_of_vouchers_backup) {
      for (let i = 0; i < this.dataSource.length; i++) {
        const row = this.dataSource[i];
        if (row.productNumberOfVouchers && row.productNumberOfVouchers == e.row.key.number_of_vouchers_backup) {
          this.dataSource[i].productNumberOfVouchers = numberOfVouchers;
        }
      }
      e.row.key.number_of_vouchers_backup = numberOfVouchers;
    }

    if (numberOfVouchers != "") {
      //// Tm ng 14/07/2020
      if (this.configs.system_parameter.the_same_number_of_voucher) {
        this.vatService.getInvoicesHaveNumberOfVouchers([numberOfVouchers]).subscribe((res: any) => {
          let dupInvoices = res.data.invoices;
          if (dupInvoices.length > 0) {
            e.row.key.statusData = "ERROR_DUPLICATE_DATABASE";
            // this.totalDuplicateInvoice++;
            // if (this.totalInvoice > 0) this.totalInvoice--
          } else {
            if (e.row.key.statusData = "ERROR_DUPLICATE_DATABASE") {
              e.row.key.statusData = "INVALID";
              // if (this.totalDuplicateInvoice > 0) this.totalDuplicateInvoice--;
            }
            // this.totalInvoice++;
          }
          let foundOnGird = this.dataSource.filter((item) => {
            return item.number_of_vouchers == numberOfVouchers;
          });
          if (foundOnGird.length > 1 && e.row.key.statusData != "ERROR_DUPLICATE_DATABASE") {
            e.row.key.statusData = "ERROR_DUPLICATE_ONGIRD";
          }
          this.dataGrid.instance.refresh();
          this.checkStatusData();
        });
      }
    }
  }

  //Thay i ct m hng
  productCodeChange(e, value) {
    this.select.products.some(item => {
      if (item.code == value) {
        e.row.key.product_name = item.name;
        e.row.key.unit_code = this.checkUnitCode(item.unit_code);
        e.row.key.quantity = null;
        e.row.key.price = item.price ? parseFloat(item.price) : item.price;
        e.row.key.amount = item.price ? parseFloat(item.price) : 0;
        e.row.key.amount_after_vat = e.row.key.amount;
      }
    });
  }

  priceChange(e, value) {
    let quantity = e.row.key.quantity || 1;
    let price = value || 0;
    if (price != null) {
      e.row.key.amount = price * quantity;
      e.row.key.amount_after_vat = e.row.key.amount;
    }
  }
  unitChange(e, value) {
    let _obj = this.select.unit_codes.filter(p => p.code == value)[0];
    if (_obj)
      e.row.key.unit_name = _obj.name;
    else
      e.row.key.unit_name = "";
  }
  fromWareHouseChange(e, value) {
    let _obj = this.select.wavehourses.filter(p => p.id == value)[0];
    if (_obj) {
      e.row.key.from_warehouse_name = _obj.name;
      e.row.key.shiping_point = _obj.address;
    }
    else {
      e.row.key.from_warehouse_name = "";
      e.row.key.shiping_point = "";
    }
  }
  toWareHouseChange(e, value) {
    let _obj = this.select.wavehourses.filter(p => p.id == value)[0];
    if (_obj) {
      e.row.key.to_warehouse_name = _obj.name;
      e.row.key.place_of_delivery = _obj.address;
    }
    else {
      e.row.key.to_warehouse_name = "";
      e.row.key.place_of_delivery = "";
    }
  }
  amountChange(e, value) {
    if (!e.row.key.quantity && !e.row.key.price) {
      e.row.key.amount_after_vat = value;
    } else {
      let quantity = e.row.key.quantity || 1;
      let price = e.row.key.price || 0;
      if (price != null) {
        e.row.key.amount = e.row.key.price * quantity;
        e.row.key.amount_after_vat = e.row.key.amount;
      }
    }
  }

  checkValidNumberOfVouchers() {
    this.dataSource.forEach(row => {
      if (row.number_of_vouchers == "") {
        Library.notify(this._translate.GRID.INVOICE_DETAILS.number_of_vouchers + " " + this._translate.VALIDATION.required, "error");
      } else {

      }
    });
  }

  checkUnitCode(unitCode) {
    let items = this.select.unit_codes;
    for (let i = 0; i < items.length; i++) {
      if (items[i].code == unitCode) {
        return unitCode;
      }
    }
    return null;
  }

  checkProvisionsOnAccounts(name) {
    let found = this.select.select_provisions_on_accounts.find(function (element) {
      return element.name === name;;
    });
    let code = "";

    if (found) {
      code = found.code;
    } else {
      Library.notify("nh khon cha c trong danh mc", "error");
    }

    return code;
  }

  roundNumber(number, n): number {
    if (number == null) {
      return number;
    } else {
      let p = Math.pow(10, n);
      return Math.round(number * p) / p;
    }
  }
  /* To m n v tnh theo tn */
  getUnitCodeByUnitName(unitName) {
    if (VStringService._getValueToString(unitName) != "") {
      if (!this.select.unit_codes)
        this.select.unit_codes = [];
      let _obj = this.select.unit_codes.filter(p => p.name == unitName)[0];
      if (_obj) {
        return _obj.code;
      } else {
        let code = VStringService._convertToViKdau(unitName);
        let oDuplicate = this.select.unit_codes.filter(p => p.code === code)[0];
        if (oDuplicate)
          code = code + Helper.addtionalUnitCode();
        this.select.unit_codes.push({ code: code, name: unitName });
        return code;
      }
    }
    return null;
  }
  /* To m kho theo tn */
  getWarehouseByName(WarehouseName) {
    if (VStringService._getValueToString(WarehouseName) != "") {
      if (!this.select.wavehourses)
        this.select.wavehourses = [];
      let _obj = this.select.wavehourses.filter(p => p.name == WarehouseName)[0];
      if (_obj) {
        return _obj.id;
      } else {
        let id = Helper.makeid();//VStringService._convertToViKdau(WarehouseName);
        this.select.wavehourses.push({ id: id, name: WarehouseName });
        return id;
      }
    }
    return null;
  }

