  submit(type) {
    if (this.windowPreviewBeforeSubmit) {
      this.windowPreviewBeforeSubmit.focus();
      return;
    }
    this.checkStatusData()
    if (!InvoicesService.checkTimeExportInv(this.configs.system_parameter)) {
      Library.notify(String.Format(this._translate.NOTIFY.time_export_inv, moment(this.configs.system_parameter.time_start).format('HH:mm'), moment(this.configs.system_parameter.time_end).format('HH:mm')), 'error');
      return;
    }
    if (this.totalDuplicateInvoice > 0) {
      Library.notify(this._translate.CONTROLL.LABEL.number_of_voucher_duplicate, "error");
      return;
    }

    let convert = this.convertGirdToInvoices("submit");
    if (convert) {
      if (type == "XUAT") {
        if (this.confirmCheckPreview) {
          // Library.confirmVA(`Tt c ha n  nhp s c xut ha n vo ngy <strong>${dayExport}</strong>. Bn c chc chn khng?`, "Thng bo", () => {
          this.showPopupImport = true;
          if (this.htXuat != sEnum.hinhThucXuat.XuatChoKy) {
            this.xuatHoaDon();
          } else {
            this.xuatChoKy();
          }
          this.confirmCheckPreview = false;
        } else {
          this.previewBeforeSubmit();
        }
      } else if (type == "LUU") {
        this.showPopupImport = true;
        this.luuChoXuat();
      }
    }
  }
  /* */
  configColumn() {
    const initialState = {
      listColumn: this.listColumn,
      listColumnClone: this.listColumn,
      configs: this.configs,
      exportType: sEnum.exportType.phieuxuatkho
    };
    this.bsModalRef = this.modalService.show(ConfigColumnImportExcelComponent, {
      class: 'modal-lg', initialState, backdrop: 'static',
      keyboard: false
    });
    let subModal = this.modalService.onHide.subscribe(() => {
      let data = this.bsModalRef.content.successSaveItem;
      if (data) {
        this.listColumn = data.listColumn;
      }
      subModal.unsubscribe();
    });
  }
  infoColumn(columnName) {
    if (this.listColumn && this.listColumn.length > 0) {
      let _obj = this.listColumn.filter(p => p.column_code == columnName)[0];
      if (_obj) {
        return _obj;
      } else return { column_code: "", column_name: "", is_visible: false };
    }
  }
  checkCKS(typeView) {
    this.connectWebsocket();
    if (this.socketService.checkNull()) {
      this.socketService.connect();
    }
    setTimeout(() => {
      const res = this.socketService.getStatusCertificate();
      if (res && res.status === 'error' && res.code == 10) {
        const initialState = {
          typeNotify: "XEM_MAU",
        };
        this.bsModalRef = this.modalService.show(SignatureOffComponent, {
          class: 'modal-lg', backdrop: 'static',
          keyboard: false,
          initialState
        });
        this.modalService.onHide.subscribe(() => {

        });
      }
      else {
        if (typeView == "XEM_TRUOC") {
          const initialState = {
            items: this.invoicesArr,
            isXuatHD: false
          };
          this.bsModalViewModel = this.modalImportService.show
            (
              ViewInvoiceComponent,
              {
                backdrop: 'static',
                keyboard: false,
                class: 'modal-xl view-invoice',
                initialState
              }
            );
          const subModal = this.modalImportService.onHide.subscribe(() => {
            subModal.unsubscribe();
          });
        }
        else if (typeView == "XUAT_HOA_DON") {
          let hinhthucxuat = "";
          if (this.htXuat != sEnum.hinhThucXuat.XuatChoKy) {
            hinhthucxuat = "v k";
          } else {
            hinhthucxuat = "ch k";
          }
          const initialState = {
            items: this.invoicesArr,
            isXuatHD: true,
            hinhthucxuat: hinhthucxuat
          };

          this.bsModalViewModel = this.modalImportService.show
            (
              ViewInvoiceComponent,
              {
                backdrop: 'static',
                keyboard: false,
                class: 'modal-xl view-invoice',
                initialState
              }
            );
          const subModal = this.modalImportService.onHide.subscribe(() => {
            if (this.bsModalViewModel.content.successSaveItem == "DONG_Y") {
              this.connectWebsocket();
              this.confirmCheckPreview = true;
              this.submit('XUAT');
            }
            subModal.unsubscribe();
          });
        }
      }
    });
  }
  dong() {
    this.bsModalImportRef.hide();
    this._location.back();
  }
