import { Component, OnInit, ViewChild } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import * as moment from 'moment';
import { DxDataGridComponent } from 'devextreme-angular';
import { Helper, Library, SocketService, AuthService, VStringService } from '@core/index';
import { VatService } from '@modules/vat/shared';
import { ActivatedRoute, Router } from '@angular/router';
import { pattern } from '@config/index';
@Component({
  selector: 'app-send-report-delivery',
  templateUrl: './send-report-delivery.component.html',
  styleUrls: ['./send-report-delivery.component.scss']
})
export class SendReportDeliveryComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  invoices: any;
  configs: any;
  invoiceList: any;
  ioSignature: any;
  invoiceIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  daXong: boolean = false;
  ganXong: boolean = false;

  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  _translate: any;
  nodeSign: any;
  isExportFailed: boolean = false;
  emailPattern = pattern.email;
  phonePattern = pattern.phone;
  reportCountERR: any = "";
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }

  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private socketService: SocketService,
    private authService: AuthService,
    private router: Router,
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.convertData();
  }

  convertData() {
    this.invoiceList.forEach(item => {
      item.invoice_date = moment(item.invoice_date).format('YYYY-MM-DD HH:mm:ss');
      delete item.updated_at;
      delete item.decpoint_is_dot;
      delete item.money_decimal_format;
      item.invoice_products = item.invoice_products.sort((itemA, itemB) => {
        return itemA.order - itemB.order;
      });
    });
  }

  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm(this._translate.CONFIRM.sign_stop);
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }

  dongy() {
    setTimeout(() => {
    if (this._validGrid()) {
      let data = [];
      this.invoiceList.forEach(item => {
        let inv = {
          id: item.id,
          carrier_email: item.carrier_email,
          carrier_mobile: item.carrier_mobile
        }
        data.push(inv);
      });
      Library.showloading();
      let sub = this.vatService.sendReportDelivery(data).subscribe(rs => {
        if (rs.status == 'success') {
          Library.notify(this._translate.NOTIFY.send_mail_success, 'success');
          this.bsModalRef.content.successSaveItem = "success_mail";
          this.bsModalRef.hide();
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  },300);
  }

  dong() {
    if (this.ganXong) {
      this.bsModalRef.content.successSaveItem = "success";
    }
    this.bsModalRef.hide();
    this.router.navigate(['/system/transport-invoice/']);
  }

  viewDetailInvoice(data) {
    if (data) {
      const ids = [];
      if (data) {
        ids.push(data.id);
      }
      Library.showloading();
      const win = window.open('', '', 'width=200, height=100');
      win.document.body.innerHTML = this._translate.NOTIFY.loading;
      win.blur();
      const sub = this.vatService.xemHoaDonInvoiceImport(ids).subscribe(rs => {
        if (rs.status === 'success') {
          const pdf_file = rs.data.invoice.pdf_file;
          win.document.write(`
            <a id="idA" title="Download File" style="font-family: \'Verdana\';color: #333;font-weight: 600;float:right; margin-bottom: 5px;
              margin-right: 20px;" download="hoadon.pdf" href="data:application/pdf;base64,${pdf_file}">Ti File</a>
            <iframe width="100%" height="97%" src="data:application/pdf;base64,${pdf_file}#download=1"></iframe>
          `);
          win.resizeTo(screen.availWidth, screen.availHeight);
          win.focus();
        } else {
          win.close();
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  _validGrid() {
    let _isvalid = true;
    for (let i = 0; i < this.invoiceList.length; i++) 
    {
      if (VStringService._getValueToString(this.invoiceList[i].carrier_email) == "") {
        // Library.notify("Email " + this._translate.VALIDATION.required, "error");
        // this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(i, 'carrier_email'));
        // _isvalid = false;
        // return false;
      }
      else if (!this.emailPattern.test(this.invoiceList[i].carrier_email)) {
        Library.notify("Email " + this._translate.VALIDATION.not_pattern, "error");
        this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(i, 'carrier_email'));
        _isvalid = false;
        return false;
      }
      if (VStringService._getValueToString(this.invoiceList[i].carrier_mobile) == "") {
        Library.notify(this._translate.CONTROLL.LABEL.phone_number + " " + this._translate.VALIDATION.required, "error");
        this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(i, 'carrier_mobile'));
        _isvalid = false;
        return false;
      }
      else if (!this.phonePattern.test(this.invoiceList[i].carrier_mobile)) {
        Library.notify(this._translate.CONTROLL.LABEL.phone_number + " " + this._translate.VALIDATION.not_pattern, "error");
        this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(i, 'carrier_mobile'));
        _isvalid = false;
        return false;
      }
    }
    return _isvalid;
  }
}
