import { Component, OnInit, Input, Output, EventEmitter, OnDestroy, ViewChildren, ViewChild, AfterViewInit } from '@angular/core';
import { ActivatedRoute, } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';

import * as moment from 'moment';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalService } from 'ngx-bootstrap/modal';
import DataSource from 'devextreme/data/data_source';
import ODataStore from 'devextreme/data/odata/store';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';

import { Library, Helper, AuthService, VChanggeMoneyService, sEnum, VStringService } from '@core/index';
import { VatService } from '@modules/vat/shared';
import { Wavehourse } from '@modules/products/shared';
import { ProductsService } from '@modules/products/shared';

import { pattern } from '@config/index';
import { AddWavehourseComponent } from '@shared/modules/some/components/add-wavehourse/add-wavehourse.component';
import { environment } from '@environments/environment';
import { TranslateService } from '@ngx-translate/core';
import { AddCustomerComponent } from '@shared/index';
import { FormExtendComponent } from '@shared/components/form-extend/form-extend.component';
import * as _ from 'lodash';
import { InvoiceService } from '@shared/service/invoice.service';
@Component({
  selector: 'transport-form-invoice',
  templateUrl: './form-invoice.component.html',
  styleUrls: ['./form-invoice.component.scss']
})
export class FormInvoiceComponent implements OnInit, OnDestroy, AfterViewInit {


  routeSub: Subscription;
  @ViewChildren(DxFormComponent) listDxForm: any;
  @ViewChild(DxFormComponent) form: DxFormComponent;
  @ViewChild(FormExtendComponent) formExtend: FormExtendComponent;

  @Input() frmData: any;
  @Input() ttDaXuatHoaDon: boolean = false
  @Input() type: any;
  @Input() dataSource: any;
  @Input() invoice_products: any;
  @Input() formatter: any;
  @Input() wavehourses: any;
  @Output('onFormChange') formEvent = new EventEmitter<any>();
  @Output('onTemplateChange') onTemplateChange = new EventEmitter<any>();
  @Output('onTemplateTT78TypeChange') onTemplateTT78TypeChange = new EventEmitter<any>();
  @Input() infoDangKy: any;
  @Input() isTT78: boolean;
  @Input() _oTemplateTT78: any;
  @Input() extentionGrid: any;
  template_temp: any;
  originInvoiceNumber: any;
  bsModalRef: BsModalRef;
  select: any = {};
  pattern = pattern;
  tax_codes: any = [];
  customer_codes: any = [];
  readOnlyCacTruongConLai: boolean;
  showTraLai: boolean = false;
  disableInvoiceDate = this.authService.isAllowEditInvoiceDate();
  htXuatOptions = [];
  wavehourse: any;
  selectedWavehourse: string = '';
  configs: any;
  isPXKDaiLy = false;
  isHoaDonDaXuat = false;
  //thay i disable ca 3 input customer_name, buyer_name, buyer_address
  tod: any = {};
  _translate: any;
  emails: any = [];
  private arrEmail: any = [];
  loaiHoaDon: string;
  strTextHDKT: any;
  strtextHDKTNgay: any;
  formItems = [];
  customForms = [];
  customerTempt = {};
  from_warehouse_btn_options = {
    text: "",
    icon: "assets/img/icons/Them.png",
    type: "default",
    onClick: () => {
      this.wavehourse = new Wavehourse();
      this.wavehourse.order = this.wavehourses.length + 1;
      const initialState = {
        wavehourse: this.wavehourse,
        onSave: (wavehourse) => {
          this.wavehourse = wavehourse;
          this.wavehourses.unshift(this.wavehourse);
          this.frmData.from_warehouse = this.wavehourse.id;
          this.frmData.from_warehouse_name = this.wavehourse.name;
        }
      };
      this.bsModalRef = this.modalService.show(AddWavehourseComponent, {
        class: 'second child-modal', initialState, backdrop: 'static',
        keyboard: false
      });
    }
  };

  to_warehouse_btn_options = {
    text: "",
    icon: "assets/img/icons/Them.png",
    type: "default",
    onClick: () => {
      this.wavehourse = new Wavehourse();
      this.wavehourse.order = this.wavehourses.length + 1;
      const initialState = {
        wavehourse: this.wavehourse,
        onSave: (wavehourse) => {
          this.wavehourse = wavehourse;
          this.wavehourses.unshift(this.wavehourse);
          this.frmData.to_warehouse = this.wavehourse.id;
          this.frmData.to_warehouse_name = this.wavehourse.name;
        }
      };
      this.bsModalRef = this.modalService.show(AddWavehourseComponent, {
        class: 'second child-modal', initialState, backdrop: 'static',
        keyboard: false
      });
    }
  };

  add_customer = {
    text: "",
    icon: "assets/img/icons/Them.png",
    type: "default",
    onClick: () => {
      const initialState = {
        isVisibleGhiVaThemMoi: false,
      };
      this.bsModalRef = this.modalService.show(AddCustomerComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
      const submodal = this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveData) {
          const customer = this.bsModalRef.content.successSaveData;
          this.frmData.customer_name = customer.name;
          this.frmData.buyer_tax_code = customer.tax_code;
          this.frmData.buyer_name = customer.contact_person_name;
          this.frmData.buyer_address = customer.business_address;
          this.frmData.customer_code = customer.code;
          this.frmData.buyer_mobile = customer.contact_mobile;

          if (customer.email) {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
            const stringDataEmailGet = customer.email.split(';');
            for (let i = 0; i < stringDataEmailGet.length; i++) {
              this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
            }
          } else {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
          }
          if (customer.bank_account_number) {
            this.frmData.bank_account_number = customer.bank_account_number;
          }
          if (customer.bank_name) {
            this.frmData.bank_name = customer.bank_name;
          }
        }
        submodal.unsubscribe();
      });
    }
  };

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private vatService: VatService,
    private productsService: ProductsService,
    public translate: TranslateService,
    private invoiceService: InvoiceService
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }
  ngAfterViewInit() {
    let a = moment(new Date()).format('HH:mm:ss.SSS');
    this.formDuLieuMoRong(this.template_temp);
    if(this.frmData.total_payment){
      const tien_chu = Helper.tienBangChu(Math.abs(this.frmData.total_payment), VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), this.frmData.currency_code, this.configs.system_parameter.display_read_money_english);
      this.frmData.total_payment_in_word = this.invoiceService.docTienAm(this.frmData.total_payment, tien_chu, this.configs, this.frmData.currency_code);
    }
  }

  ngOnInit() {
    this.htXuatOptions = VSelectBoxService._htXuatOptions(this._translate, this.frmData.status);
    let a = moment(new Date()).format('HH:mm:ss.SSS');
    this.configs = this.authService.getConfigs();
    this.loaiHoaDon = this.route.snapshot.data.type;
    if (this.loaiHoaDon == sEnum.loaihoadon.HGDL04)
      this.isPXKDaiLy = true;
    if (this.isPXKDaiLy) {
      this.strTextHDKT = this._translate.CONTROLL.LABEL.deconomic_contract_number;
      this.strtextHDKTNgay = "Hp ng kinh t ngy";
    }
    else {
      this.strTextHDKT = this._translate.CONTROLL.LABEL.delivery_order_number;
      this.strtextHDKTNgay = this._translate.CONTROLL.LABEL.date;
    }

    if (this.route.snapshot.queryParams['status'] == 'return') {
      this.showTraLai = true;
      this.frmData.nguoitralai = this.authService.getCurrentUser().user.name;
      this.frmData.ngaytralai = moment(new Date()).format('YYYY-MM-DD');
    }

    this.routeSub = this.route.data.subscribe((data: any) => {
      //put data DM for select
      let danh_muc = this.authService.getDMLocal();
      this.select.unit_codes = danh_muc.DM_DON_VI_TINH ? danh_muc.DM_DON_VI_TINH.data : null;
      this.select.payment_method_names = danh_muc.DM_HINH_THUC_THANH_TOAN ? danh_muc.DM_HINH_THUC_THANH_TOAN.data : null;
      this.select.currency_codes = danh_muc.DM_LOAI_TIEN ? danh_muc.DM_LOAI_TIEN.data : null;
      this.select.customer_types = danh_muc.DM_NHOM_KHACH_HANG ? danh_muc.DM_NHOM_KHACH_HANG.data : null;
      this.select.export_places = danh_muc.DM_NOI_XUAT_HOA_DON ? danh_muc.DM_NOI_XUAT_HOA_DON.data : null;
      if (this.isTT78) {
        let temptTT78 = [];
        if (!this.frmData.id) {
          temptTT78 = data.init[1];
        }
        else {
          temptTT78 = data.init[3];
        }
        if (temptTT78 && temptTT78.length > 0) {
          this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.infoDangKy.is_invoice_with_code);
          if (Number.parseInt(this.frmData.invoice_number) > 0) {
            if (this.frmData.is_invoice_with_code != this.infoDangKy.is_invoice_with_code) {
              this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.frmData.is_invoice_with_code);
            }
          }
        }
        if (this.frmData.id)
          this.changeTemplateInvoice(this.frmData.invoice_date);
      }
      else {
        this.select.invoice_releases = data.init[0];
        if (!this.frmData.id) {
          this.select.invoice_releases = this.select.invoice_releases.filter(p => p.number_survival > 0 && p.release_status != 'DA_HUY_SO');
        }
      }
    });

    this.select.customer_object_codes = VSelectBoxService._customerObject(this._translate);
    this.select.adjustment_forms = VSelectBoxService._adjustmentForms(this._translate);
    this.select.adjustment_objects = VSelectBoxService._adjustmentObjects(this._translate);

    //Set disabled cho n v t chc
    this.setDisabledByCustomer_object_code(this.frmData.customer_object_code);

    //tm kim m s thu
    this.tax_codes = new DataSource({
      store: new ODataStore({
        url: `${environment.API_URL}/customers`,
        beforeSend: (e) => {
          e.params = {
            "limit": "10", "page": "1",
            "filter_groups[0][or]": "true",
            "filter_groups[0][filters][0][key]": "tax_code",
            "filter_groups[0][filters][0][value]": this.frmData.buyer_tax_code,
            "filter_groups[0][filters][0][operator]": "ct",
            "filter_groups[0][filters][1][key]": "name",
            "filter_groups[0][filters][1][value]": this.frmData.buyer_tax_code,
            "filter_groups[0][filters][1][operator]": "ct",
          };
          e.headers = {
            "Authorization": `Bearer ${JSON.parse(localStorage.getItem('access_token')).value}`,
            "TaxCode": JSON.parse(localStorage['currentUser']).account.tax_code,
            "Content-Type": "application/json",
          };
        }
      }),
      map: (dataItem) => {
        return dataItem.customers
      }
    });

    // // tm kim m khch hng
    this.customer_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'code');
      }
    });
    if (!this.isTT78) {
      this.select.invoice_releases.forEach(item => {
        if (item.template_id === this.frmData.template_id) {
          // Nu l mu html
          this.template_temp = item;
        }
      });
    }
    else {
      this.select.templatesTT78.forEach(item => {
        this.frmData.invoice_release_id = "";
        if (item.value === this.frmData.template_id) {
          this.template_temp = item;
        }
      });
    }
    if (this.frmData.buyer_email) {
      this.emails = [];
      const emailToString = this.frmData.buyer_email;
      const a11 = emailToString.replace(/;/g, ',');
      this.emails = a11.split(',');
    }
  }

  formDuLieuMoRong(item) {
    this.customForms = [];
    this.customerTempt = {};
    Helper.resetObject(this.formItems);
    if (item.fields && item.fields.length > 0) {
      const _arrayGrid = item.fields.filter(x => x.type === '5');
      if (_arrayGrid && _arrayGrid.length > 0) {
        this.extentionGrid.ma_hang = false;
        this.extentionGrid.so_lo = false;
        this.extentionGrid.han_dung = false;
        this.extentionGrid.nuoc_san_xuat = false;
        this.extentionGrid.don_hang_hop_dong = false;
        this.extentionGrid.ma_quan_ly = false;
        for (let i = 0; i < _arrayGrid.length; i++) {
          const column = _arrayGrid[i]['name'];
          const value = _arrayGrid[i]['is_show'];
          let _ma = VStringService._convertToViKdau(column);
          this.extentionGrid[VStringService._convertToViKdau(column)] = value;
        }
      }
      let _dulieumorong = _.cloneDeep(item.fields).filter(x => x.type === '4');
      const invoice_options = this.frmData.invoice_options;

      if (invoice_options.length > 0) {
        _dulieumorong = _dulieumorong.map((dl) => {
          let option = invoice_options.find(x => x.code == dl.code)
          if (option) {
            dl.value = option.value;
          }
          return dl;
        });
      }

      this.frmData.invoice_options = _dulieumorong;
      if (_dulieumorong.length > 0) {
        this.formExtend.createForm(this.customForms, _dulieumorong)
        _dulieumorong.forEach(value => {
          this.customerTempt[value.code] = value.value;
        });
      }
    }
    else {
      Helper.resetObject(this.formItems);
      this.extentionGrid.ma_hang = false;
      this.extentionGrid.so_lo = false;
      this.extentionGrid.han_dung = false;
      this.extentionGrid.nuoc_san_xuat = false;
      this.extentionGrid.don_hang_hop_dong = false;
      this.extentionGrid.ma_quan_ly = false;
    }
  }

  createForm() {
    this.form.instance.beginUpdate();
    this.form.instance.itemOption("business_addressa", { label: { text: "a ch" }, editorOptions: { placeholder: "Nhp a ch" } });
    this.form.instance.endUpdate();
  }

  disableTemplateInput() {
    if (this.frmData.invoice_number) {
      return true;
    }
    return false;
  }

  readonlyAllForm(trangthaiHD) {
    this.listDxForm._results.forEach(item => {
      item.readOnly = true;
    });
    this.readOnlyCacTruongConLai = true;
    if (trangthaiHD == sEnum.statusInv.DaXuat)
      this.isHoaDonDaXuat = true;
    else
      this.isHoaDonDaXuat = false;
  }
  allowEditAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = false;
    });
    this.readOnlyCacTruongConLai = false;
  }

   // reset trng m rng
   public resetTMR() {
    this.formExtend.resetForm();
    this.formExtend.resetOptions();
  }

  public removeReadonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = false;
      // item.instance.resetValues();
    });
    this.readOnlyCacTruongConLai = false;
  }

  setDisabledByCustomer_object_code(value) {
    this.tod.customer_name = false;
    this.tod.buyer_name = false;
    this.tod.buyer_address = false;

    switch (value) {
      case 'DOANH_NGHIEP':
        break;
      case 'CA_NHAN':
        this.frmData.customer_name = '';
        this.tod.customer_name = true;
        break;
      case 'KHACH_LE':
        this.frmData.customer_name = '';
        this.frmData.buyer_address = '';
        this.tod.customer_name = true;
        this.tod.buyer_address = true;
        break;
      default:
        break;
    }
  }

  //gi data ln parent component
  onFieldDataChanged() {
    this.formEvent.emit(this.frmData);
  }

  getHtXuatValue() {
    return this.frmData.status;
  }
  getFrmData() {
    return this.frmData;
  }

  //s kin
  taxCodeKeyUp = (e) => {
    let str = this.frmData.buyer_tax_code;
    let len = str.length;
    if (len >= 3) {
      this.vatService.searchMST(str).subscribe(rs => {
        if (rs.status == 'success') {
          let arr = [];
          rs.data.customers.forEach(item => {
            arr.push(item);
          });
          this.tax_codes = arr;
        }
      });
    }
  }

  taxCodeChoose(e) {
    let customer = e.itemData;
    if (this.frmData.customer_object_code != 'CA_NHAN' && this.frmData.customer_object_code != 'KHACH_LE') this.frmData.customer_name = customer.name;
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code != 'KHACH_LE') this.frmData.buyer_address = customer.business_address;
    this.frmData.customer_code = customer.code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    this.frmData.buyer_email = customer.email;
    this.frmData.customer_id = customer.id;
    if (customer.bank_account_number) this.frmData.bank_account_number = customer.bank_account_number;
    if (customer.bank_name) this.frmData.bank_name = customer.bank_name;

    this.onFieldDataChanged();
  }

  customerCodeChoose(e) {

    let customer = e.itemData;
    if (this.frmData.customer_object_code != 'CA_NHAN' && this.frmData.customer_object_code != 'KHACH_LE') this.frmData.customer_name = customer.name;
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code != 'KHACH_LE') this.frmData.buyer_address = customer.business_address;
    this.frmData.buyer_tax_code = customer.tax_code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    if (customer.bank_account_number) this.frmData.bank_account_number = customer.bank_account_number;
    if (customer.bank_name) this.frmData.bank_name = customer.bank_name;

    this.onFieldDataChanged();
  }

  customer_object_codeChange = (e) => {
    this.setDisabledByCustomer_object_code(e.value);
  }

  templateChange = (e) => {
    this.select.invoice_releases.forEach(item => {
      if (e.value === item.value) {
        this.frmData.template_id = item.template_id;
        this.onTemplateChange.emit(item.is_multi_vat);
        // Nu l mu html
        this.formDuLieuMoRong(item);
      }
    });
  }
  onChangeTem = (e) => {
    this.select.templatesTT78.forEach(item => {
      if (e.value === item.value) {
        this.frmData.invoice_release_id = "";
        this.frmData.template_id = item.value;
        this.frmData.invoice_series = item.invoice_series;
        this.frmData.template_code = item.template_code;
        this.onTemplateChange.emit(item.is_multi_vat);
        this.onTemplateTT78TypeChange.emit(item);
        for (const key in item) {
          if (item.hasOwnProperty(key)) {
            const value = item[key];
            this._oTemplateTT78[key] = value;
          }
        }
        // Nu l mu html
        this.formDuLieuMoRong(item);
      }
    });
  }

  from_warehouseChange = (e) => {
    let _obj = this.wavehourses.filter(p => p.id == e.value)[0];
    if (_obj) {
      this.frmData.from_warehouse_name = _obj.name;
      this.frmData.shiping_point = _obj.address;
    }

  }
  to_warehouseChange = (e) => {
    let _obj = this.wavehourses.filter(p => p.id == e.value)[0];
    if (_obj) {
      this.frmData.to_warehouse_name = _obj.name;
      this.frmData.place_of_delivery = _obj.address;
    }
  }
  currencyCodeChange = (e) => {
    if (this.frmData.currency_code == 'VND') {
      this.frmData.currency_rate = null;
    }
    const tien_chu = Helper.tienBangChu(Math.abs(this.frmData.total_payment), VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), this.frmData.currency_code, this.configs.system_parameter.display_read_money_english);
    this.frmData.total_payment_in_word = this.invoiceService.docTienAm(this.frmData.total_payment, tien_chu, this.configs, this.frmData.currency_code);
  }
  currencyRateChange = (e) => {
    VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, null, this.configs.system_parameter)
  }

  exportPlaceChange = (e) => {
    let _obj = this.select.export_places.filter(p => p.code == e.value)[0];
    if (_obj) {
      this.frmData.export_address = _obj.dia_chi;
      this.frmData.export_place_name = _obj.name;
    }
  }
  // paymentMethodChange = (e) => {
  //   let _obj = this.select.payment_method_names.filter(p => p.code == e.value)[0];
  //   if (_obj) {
  //     this.frmData.payment_method_view_name = _obj.name;
  //   }
  // }


  ngOnDestroy() {
    this.routeSub.unsubscribe();
  }
  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.frmData.buyer_email = emailToString.replace(/,/g, ';');
    } else {
      this.frmData.buyer_email = null;
    }
    this.formEvent.emit(this.frmData);
  }
  updateCustomerInfo = (e) => {
    // if (this.frmData.customer_id) {
    //   this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
    //     if (response.data) {
    //       this.customerCurent = response.data.customer;
    //       if (this.customerCurent) {
    //         if (VStringService._getValueToString(this.customerCurent.code) !== VStringService._getValueToString(this.frmData.customer_code)) {
    //           this.showPopup = true;
    //           return;
    //         }
    //       }
    //     }
    //   });
    // }
  };
  changeDateTime = (e) => {
    this.changeTemplateInvoice(e.value);
  }

  changeTemplateInvoice(strDate) {
    if (this.isTT78) {
      let year = "";
      if (VStringService._getValueToString(strDate) != "")
        year = VStringService._getValueToString(strDate).substring(4, 2);
      else {
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        year = VStringService._getValueToString(invoiceDate).substring(4, 2);
      }
      let templates = this.select.templatesTT78;
      this.select.templatesTT78 = [];
      if (templates && templates.length > 0) {
        templates.forEach(item => {
          if (VStringService._getValueToString(item.name) != "")
            item.name = item.name.replace(item.name.substring(4, 2), year);
          // for (const key in item) {
          //   if (item.hasOwnProperty(key)) {
          //     const value = item[key];
          //     this._oTemplateTT78[key] = value;
          //   }
          // }
          if (item.value === this.frmData.template_id) {
            this.onTemplateTT78TypeChange.emit(item);
            //  item.invoice_series = item.invoice_series.replace(item.invoice_series.substring(3, 1), year);
            // this.frmData.invoice_series = item.invoice_series;
            // this.frmData.template_code = item.template_code;
          }
          this.select.templatesTT78.push(item);
        });
      }
    }
  }

}
