import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexComponent } from './components/index/index.component';
import { InvoiceComponent } from './components/invoice/invoice.component';
import { VatDeactivateGuard } from '@modules/vat/deactivate/vat-deactivate.guard';
import { InvoiceResolver, ShowInvoiceResolver } from '@modules/vat/shared';
const routes: Routes = [
  { path: '', component: IndexComponent },
  { path: 'create', component: InvoiceComponent, resolve: { init: InvoiceResolver }, canDeactivate: [VatDeactivateGuard], data: { brec: 'create_delivery' } },
  { path: ':invoiceId', component: InvoiceComponent, resolve: { init: ShowInvoiceResolver }, canDeactivate: [VatDeactivateGuard], data: { brec: 'detail_delivery' } },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class routing { }
