  /* In lnh iu ng */
  inlenhdieudong() {
    if (this.selectedItems.length > 0) {
      let ids = [];
      ids = this.selectedItems.map(e => e.id);
      if (ids.length == 1) {
        let w = 1920, h = 900;
        w = window.screen.availWidth * 100 / 100;
        h = window.screen.availHeight * 93 / 100;
        let win = window.open("about:blank", "name", "width=200, height=100");
        win.document.body.innerHTML = this._translate.NOTIFY.loading;
        win.blur();
        Library.showloading();
        let sub = this.vatService.xemlenhdieudong(ids).subscribe(rs => {
          if (rs.status == "success") {
            let pdf_file = rs.data[0].file_content;
            let objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
            win.resizeTo(screen.availWidth, screen.availHeight);
            win.focus();
            win.open(objectUrl, "name", "width=" + w + ", height=" + h);
          } else {
            win.close();
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      } else {
        const initialState = {
          items: ids,
          isXuatHD: false,
          hinhthucxuat: '',
          typeAction: sEnum.typeCode.in_lenh_dd,
          type_invocie: sEnum.typeCode.lenh_dieu_dong
        };
        this.vPreviewInvService.xemMauPhanTrang(initialState);
      }
    }

  }
  itemExtrasClick(e) {
    if (e.itemData.id == "NAP_HD_TU_EXCEL_DT") {
      this.napHDTuExcelDt();
    } else if (e.itemData.id == "GIU_SO") {
      this.giuSo();
    } else if (e.itemData.id == "KY_SO") {
      this.kyHangLoat();
    } else if (e.itemData.id == 'XOA') {
      this.delete(null);
    } else if (e.itemData.id == 'XEM') {
      this.view(null);
    } else if (e.itemData.id == 'INCHUYENDOI') {
      this.inChuyenDoi(null);
    } else if (e.itemData.id === 'GUI_BEN_MUA') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'SUA') {
      this.edit(null);
    } else if (e.itemData.id == "TAI_HD_XML") {
      this.taiHDXml();
    } else if (e.itemData.id == "TAI_HD_PDF") {
      this.taiHDPdf();
    } else if (e.itemData.id == "KY_LENH_DIEU_DONG") {
      this.kylenhdieudong();
    } else if (e.itemData.id == "IN_LENH_DIEU_DONG") {
      this.inlenhdieudong();
    } else if (e.itemData.id == "GUI_BB_GIAO_HANG") {
      this.guibbgiaohang(null);
    } else if (e.itemData.id == "IN_BB_GIAO_HANG") {
      this.inbbgiaohang();
    }
  }
  /* Gi BB giao hng */
  guibbgiaohang(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    let check = true;
    if (data) {
      if (data.status !== 'DA_XUAT') {
        check = false;
      }
    } else {
      if (this.selectedItems.filter(p => p.status === 'DA_XUAT').length === 0) {
        check = false;
      }
    }

    if (check && (this.selectedItems.length > 0 || data)) {
      Library.showloading();
      let ids = [];
      if (data) {
        ids.push(data.id);
      } else {
        ids = this.selectedItems.filter(p => p.status === 'DA_XUAT').map(p => p.id);
      }
      this.vatService.layCacHoaDonKySoHangLoat(ids, this.loaiHoaDon).subscribe((rs: any) => {
        if (rs[0].status === 'success') {
          const initialState = {
            invoiceList: rs[0].data.invoices,
            configs: rs[1].data.configs,
            loaiHoaDon: this.loaiHoaDon
          };
          this.bsModalSignRef = this.modalService.show(SendReportDeliveryComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(this.paramSearch);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify(this._translate.NOTIFY.please_choose_delivery_exported, 'error');
    }
  }
  /* In BB giao hng */
